import { useCallback, useContext } from 'react'

import useToggle from '../../Common/hooks/use-toggle'
import Modal from '../../Common/Modal'
import DialogBox from '../../Common/DialogBox'
import ActionButton from '../../Common/ActionButton'
import { GitHubWikiLayoutsLink } from '../../Dialogs/support'

import { ClipboardContext } from './store'
import KeymapRaw from './KeymapRawInput'
import styles from './styles.module.css'

export default function KeymapSourceDialog ({ onSubmit, onCancel }) {
  const [state, actions] = useContext(ClipboardContext)
  const { pending } = state
  const [metadataExpanded, toggleMetadataExpanded] = useToggle(!pending.metadataRaw)

  const handleMetadataChange = useCallback(event => {
    const raw = event.target.value
    let parsed = null
    let errors = null

    try {
      parsed = JSON.parse(raw)
    } catch (err) {
      errors = err
    }

    actions.setMetadata({ pending: true, raw, parsed, errors })
  }, [actions])

  const handleKeymapChange = useCallback(({ raw, parsed }) => {
    actions.setKeymap({ pending: true, raw, parsed })
  }, [actions])

  const handleSubmit = useCallback(() => {
    actions.confirmPending()
    onSubmit({
      keymap: pending.keymapParsed,
      metadata: pending.metadataParsed
    })
  }, [actions, pending, onSubmit])

  const disabled = (
    !pending.metadataParsed ||
    !pending.keymapParsed
  )

  return (
    <Modal onDismiss={onCancel}>
      <DialogBox style={{ maxWidth: '640px', maxHeight: '90vh', overflow: 'auto' }} dismissText={null}>
        <article className={styles.source} data-expanded={metadataExpanded}>
          <h2 onClick={toggleMetadataExpanded}>Keyboard{pending.metadataParsed && (
            <>: <code>{pending.metadataParsed.name}</code></>
          )}</h2>
          <section className={styles.sourceContent}>
            <p>
              In order to understand what keyboard you're using and now to display
              it visually, please paste the JSON source metadata file below.
            </p>
            {pending.metadataParsed && (
              <p>Parsed metadata for: {pending.metadataParsed.name}</p>
            )}
            <textarea
              cols={80}
              rows={pending.metadataParsed ? 22 : 5}
              wrap="off"
              spellCheck={false}
              value={pending.metadataRaw}
              onChange={handleMetadataChange}
            />
            {!pending.metadataParsed && (
              <div className={styles.metadataHelp}>
                <h3>Not sure what to put here?</h3>
                <p>
                  The <em>Keymap Editor</em> app depends on a defined keyboard
                  layout both to provide a friendly graphical representation to
                  edit and a readable textual representation. You can learn more
                  about this at {GitHubWikiLayoutsLink}.
                </p>
                <p>
                  Note: If your keyboard works with QMK's Configurator you may
                  be able to quickly get a visual representation by pasting the
                  contents of that keyboard's <code>info.json</code> file here.
                </p>
              </div>
            )}
          </section>
        </article>
        <h2>Keymap</h2>
        <p>Paste the devicetree source from your <code>.keymap</code> file below.</p>
        <p>
          You can <em>technically</em> make changes directly to the DeviceTree
          code here, but this isn't intended as an editor.
        </p>
        <KeymapRaw
          cols={80}
          rows={22}
          wrap="off"
          spellCheck={false}
          value={pending.keymapRaw}
          onChange={handleKeymapChange}
        />
        <p style={{ textAlign: 'center' }}>
          <ActionButton disabled={disabled} onClick={handleSubmit}>Set Keymap</ActionButton>
          <ActionButton onClick={onCancel} secondary>Cancel</ActionButton>
        </p>
      </DialogBox>
    </Modal>
  )
}
