import cloneDeep from 'lodash/cloneDeep'
import isEqual from 'lodash/isEqual'
import { useCallback, useContext, useMemo, useState } from 'react'
import { useSelector } from 'react-redux'
import { ClipboardContext } from './store'

import applyKeymapChanges from 'keymap-editor-zmk-backend/lib/apply-keymap-changes'
import extractKeymapData from 'keymap-editor-zmk-backend/lib/extract-keymap-data'

import styles from './styles.module.css'
import { Button } from '../Controls'
import Icon from '../../Common/Icon'
import { stripLegacyProperties } from '../../keymap'

const keymapSelector = state => state.keyboard?.keymap

export default function ClipboardActions () {
  const [updateNeeded, update] = useKeymapUpdater()
  const props = { updateNeeded, update }

  return (
    <>
      <ModalButton {...props} />
      <CopyButton {...props} />
    </>
  )
}

export function useKeymapUpdater () {
  const appKeymapState = useSelector(keymapSelector)
  const [state, actions] = useContext(ClipboardContext)

  const updateNeeded = useMemo(() => (
    appKeymapState &&
    state.keymapParsed &&
    !isEqual(appKeymapState, state.keymapParsed)
  ), [appKeymapState, state.keymapParsed])

  const update = useCallback(async function () {
    // When opening the clipboard dialog and a previously parsed keymap differs
    // from the app's current keymap state we're probably wanting to update the
    // keymap source
    const { layouts } = state.metadataParsed
    const preferredLayout = appKeymapState.chosen['zmk,matrix_transform']
      ? appKeymapState.chosen['zmk,matrix_transform'].replace(/^&/, '')
      : null

    const defaultLayout = Object.keys(layouts)[0]
    const { layout } = (
      layouts[preferredLayout] ||
      layouts[defaultLayout]
    )

    const raw = await applyKeymapChanges(layout, cloneDeep(appKeymapState), state.keymapRaw)
    const parsed = await extractKeymapData(raw)
    stripLegacyProperties(parsed)

    actions.setKeymap({ raw, parsed })
    return raw
  }, [appKeymapState, state, actions])

  return [updateNeeded, update]
}

export function ModalButton ({ updateNeeded, update }) {
  const [, { openModal }] = useContext(ClipboardContext)
  const handleOpenModal = useCallback(async () => {
    if (updateNeeded) {
      await update()
    }

    openModal()
  }, [openModal, update, updateNeeded])

  return (
    <Button onClick={handleOpenModal} title="View/update keymap code">
      Keymap
    </Button>
  )
}

export function CopyButton ({ updateNeeded, update }) {
  const title = updateNeeded ? 'Copy updated keymap' : 'No changes'
  const [{ keymapParsed }] = useContext(ClipboardContext)
  const [isCopiedVisible, showCopied] = useState(false)

  const handleCopy = useCallback(async function () {
    try {
      const raw = await update()
      await navigator.clipboard.writeText(raw)
      showCopied(true)
      setTimeout(() => showCopied(false), 1500)
    } catch (err) {
      console.error('Clipboard write failed')
      console.log(err)
    }
  }, [update])

  if (!keymapParsed) {
    return null
  }

  return (
    <Button
      title={title}
      disabled={!updateNeeded}
      className={styles.copyButton}
      onClick={handleCopy}
      data-show-copied={isCopiedVisible}
    >
      <Icon name={updateNeeded ? 'clipboard-list' : 'clipboard-check'} />
    </Button>
  )
}
