import cloneDeep from 'lodash/cloneDeep'
import { useCallback, useContext, useEffect, useState } from 'react'

import { ClipboardContext, withPickerStore } from './store'
import KeymapSourceDialog from './KeymapSourceDialog'
import ClipboardActions from './ClipboardActions'

export function ClipboardPicker ({ onSelect, onError }) {
  const [state, { closeModal }] = useContext(ClipboardContext)
  const [initState, setInitState] = useState(null)
  const handleSelectKeymap = useCallback(({ keymap, metadata }) => {
    onSelect(cloneDeep({
      source: 'clipboard',
      keyboard: '',
      layoutMetadata: metadata,
      layout: Object.values(metadata.layouts)[0].layout,
      keymap,
      sensors: []
    }))

    closeModal()
  }, [onSelect, closeModal])

  useEffect(() => {
    const { keymapParsed: keymap, metadataParsed: metadata } = state

    if (initState) {
      return
    }

    setInitState(true)

    if (!(state.keymapParsed && state.metadataParsed)) {
      return
    }

    onSelect(cloneDeep({
      source: 'clipboard',
      keyboard: '',
      layoutMetadata: metadata,
      layout: Object.values(metadata.layouts)[0].layout,
      keymap,
      sensors: []
    }))
  }, [initState, setInitState, onSelect, state])

  return (
    <>
      <ClipboardActions />
      {state.modalOpen && (
        <KeymapSourceDialog
          onSubmit={handleSelectKeymap}
          onCancel={closeModal}
        />
      )}
    </>
  )
}

export default withPickerStore(ClipboardPicker)
