import styles from './action-button.module.css'

function ActionButton ({ children, secondary, className, ...props }) {
  return (
    <button
      className={[
        className,
        styles.action,
        secondary && styles.secondary
      ].join(' ')}
      {...props}
    >
      {children}
    </button>
  )
}

ActionButton.defaultProps = {
  className: ''
}

export default ActionButton
