import { createContext, useMemo, useReducer } from 'react'
import { createSlice } from '@reduxjs/toolkit'

const STORAGE_KEY_KEYMAP = 'keymap_editor_clipboard_keymap_raw'
const STORAGE_KEY_KEYMAP_PARSED = 'keymap_editor_clipboard_keymap_parsed'
const STORAGE_KEY_METADATA = 'keymap_editor_clipboard_metadata_raw'

const INITIAL_KEYMAP = JSON.parse(localStorage.getItem(STORAGE_KEY_KEYMAP) || '""')
const INITIAL_KEYMAP_PARSED = JSON.parse(localStorage.getItem(STORAGE_KEY_KEYMAP_PARSED || 'null'))
const INITIAL_METADATA = JSON.parse(localStorage.getItem(STORAGE_KEY_METADATA) || '""')

const INITIAL_STATE = {
  keymapRaw: INITIAL_KEYMAP,
  keymapParsed: INITIAL_KEYMAP_PARSED,
  metadataRaw: INITIAL_METADATA,
  metadataParsed: INITIAL_METADATA ? JSON.parse(INITIAL_METADATA) : null,
  modalOpen: !INITIAL_KEYMAP,
  pending: {
    keymapRaw: INITIAL_KEYMAP,
    keymapParsed: INITIAL_KEYMAP_PARSED,
    metadataRaw: INITIAL_METADATA,
    metadataParsed: INITIAL_METADATA ? JSON.parse(INITIAL_METADATA) : null
  }
}

export const pickerSlice = createSlice({
  name: 'clipboard-picker',
  initialState: INITIAL_STATE,
  reducers: {
    setKeymap (state, action) {
      const { raw, parsed, pending = false } = action.payload

      state.pending.keymapRaw = raw
      state.pending.keymapParsed = parsed
      if (!pending) {
        state.keymapRaw = raw
        state.keymapParsed = parsed
      }
    },
    setMetadata (state, action) {
      const { raw, parsed, pending = false } = action.payload
      const target = pending ? state.pending : state
      target.metadataRaw = raw
      target.metadataParsed = parsed
    },
    confirmPending (state) {
      state.keymapRaw = state.pending.keymapRaw
      state.keymapParsed = state.pending.keymapParsed
      state.metadataRaw = state.pending.metadataRaw
      state.metadataParsed = state.pending.metadataParsed

      localStorage.setItem(STORAGE_KEY_KEYMAP, JSON.stringify(state.keymapRaw))
      localStorage.setItem(STORAGE_KEY_KEYMAP_PARSED, JSON.stringify(state.keymapParsed))
      localStorage.setItem(STORAGE_KEY_METADATA, JSON.stringify(state.metadataRaw))
    },
    openModal (state) {
      state.modalOpen = true
      state.pending.keymapRaw = state.keymapRaw
      state.pending.keymapParsed = state.keymapParsed
      state.pending.metadataRaw = state.metadataRaw
      state.pending.metadataParsed = state.metadataRaw ? JSON.parse(state.metadataRaw) : null
    },
    closeModal (state) {
      state.modalOpen = false
    }
  }
})

function useActionDispatchers (dispatch) {
  return useMemo(() => (
    Object.keys(pickerSlice.actions).reduce((actions, action) => {
      actions[action] = (...args) => dispatch(pickerSlice.actions[action](...args))
      return actions
    }, {})
  ), [dispatch])
}

export function usePickerStore () {
  const [state, dispatch] = useReducer(pickerSlice.reducer, INITIAL_STATE)
  const dispatchers = useActionDispatchers(dispatch)

  return [state, dispatchers]
}

export const ClipboardContext = createContext([{}, {}])

export function PickerStore ({ children }) {
  const [state, dispatchers] = usePickerStore()

  return (
    <ClipboardContext.Provider value={[state, dispatchers]}>
      {children}
    </ClipboardContext.Provider>
  )
}

export function withPickerStore (Component) {
  return props => (
    <PickerStore>
      <Component {...props} />
    </PickerStore>
  )
}
