import { useCallback, useEffect, useState } from 'react'

import extractKeymapData from 'keymap-editor-zmk-backend/lib/extract-keymap-data'
import { stripLegacyProperties } from '../../keymap'

export default function KeymapRaw ({ value, onChange, ...props }) {
  const [{ raw }, setState] = useState({ raw: value, parsed: null })

  useEffect(() => {
    setState(state => ({ ...state, raw: value }))
  }, [value, setState])

  const handleChange = useCallback(event => {
    const raw = event.target.value
    setState(state => ({ ...state, raw }))
  }, [setState])

  useEffect(() => {
    async function parse () {
      // debounce? handle errors?
      const parsed = await extractKeymapData(raw)
      stripLegacyProperties(parsed)
      setState(state => ({ ...state, parsed }))
      onChange({ raw, parsed })
    }

    if (raw) {
      parse()
    }
  }, [raw, setState, onChange])

  return (
    <textarea
      onChange={handleChange}
      value={raw}
      {...props}
    />
  )
}
